import React, { useState, useEffect } from "react";
import Header from "./components/Header";
import Chat from "./components/Chat";
import "./styles/ChatWidget.css";
import Bottom from "./components/Bottom";
import Fab from "@mui/material/Fab";
import ChatOutlinedIcon from "@mui/icons-material/ChatOutlined";

export const ChatWidget = ({
  data = [],
  avatar,
  name = "Your name",
  online = false,
  isTyping = false,
  minimalized = true,
  guestDev,
  onSubmit,
  onOpen,
}) => {
  const [messages, setMessages] = useState(data);
  const [isMinimalized, setIsMinimalized] = useState(minimalized);

  const sendHandler = (value) => {
    const newData = {
      text: value,
      date: new Date(),
      me: guestDev ? false : true,
    };
    setMessages([...messages, newData]);
    if (onSubmit) onSubmit(newData);
  };

  const openHandler = () => {
    setIsMinimalized(!isMinimalized);
    if (onOpen) onOpen(!isMinimalized);
  };

  return (
    <div className="ChatWidget__container">
      {!isMinimalized && (
        <div className="ChatWidget__content">
          <Header
            avatar={avatar}
            name={name}
            online={online}
            onClose={openHandler}
          />
          <Chat messages={messages} />
          <Bottom sendAction={sendHandler} isTyping={isTyping} />
        </div>
      )}
      <Fab
        onClick={openHandler}
        className="ChatWidget__floatButton"
        size="large"
        aria-label="add"
      >
        <ChatOutlinedIcon />
      </Fab>
    </div>
  );
};
